import { Component, OnInit } from '@angular/core';
import { CompanyPerusahaanService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { ActivatedRoute, Router } from '@angular/router';
import { Perusahaan } from 'src/app/sdk/hr/models';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';
import { FormGroupTypeSafe, FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { FormControl, Validators } from '@angular/forms';
import { PesanService } from 'src/app/services/tool/pesan.service';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private companyPerusahaanService: CompanyPerusahaanService,
        private singleUploadService: SingleUploadService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute,
        private router: Router
    ) { }

    id_perusahaan: string = ''
    toogleUpload = false

    dataPerusahaan: Perusahaan = {}
    ngOnInit() {
        this.id_perusahaan = this.activatedRoute.snapshot.paramMap.get('id_perusahaan')
        this.getData()
        this.form()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'company.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ logo: data })
                    this.simpan()
                }
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }
    foto = 'https://os.alipayobjects.com/rmsportal/QBnOOoLaAfKPirc.png'
    getData() {
        this.companyPerusahaanService.getCompanyPerusahaansId(this.id_perusahaan).subscribe(
            data => {
                this.dataPerusahaan = data.data;

                this.dataForm.setValue(data.data)

                this.singleUploadService.param.next({
                    for_module: "company_perusahaan",
                    for_table: "erp.perusahaan",
                    for_module_id: data.data.id_perusahaan
                })

                if (data.data.logo) {
                    this.foto = "http://erp-document.vm.devetek.com/download?id_document=" + data.data.logo
                }

            }
        )
    }

    dataForm: FormGroupTypeSafe<Perusahaan>;
    form() {
        this.dataForm = this.fb.group<Perusahaan>(
            {
                nama: new FormControl('', [Validators.required]),
                alamat: new FormControl('', [Validators.required]),
                email: new FormControl(''),
                website: new FormControl('', [Validators.required]),
                telfon: new FormControl(''),
                kota: new FormControl(''),
                propinsi: new FormControl(''),
                negara: new FormControl(''),
                kode_pos: new FormControl(''),
                hp: new FormControl(''),
                fax: new FormControl(''),
                id_perusahaan: new FormControl(''),
                id_user_create: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                logo: new FormControl(''),
                pic: new FormControl(''),

            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.dataPerusahaan = this.dataForm.value;
        this.companyPerusahaanService.putCompanyPerusahaansId({ id: this.id_perusahaan, body: this.dataPerusahaan }).subscribe(
            data => {
                this.toogleUpload = false
                this.getData()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }


    goToBack() {
        this.router.navigate(['/hr/company/module/perusahaan/list'])
    }

}
