import { Component, OnInit } from '@angular/core';
import { PayrollPerusahaanpService } from 'src/app/sdk/payroll/services';
import { Perusahaanp } from 'src/app/sdk/payroll/models';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private payrollPerusahaanpService: PayrollPerusahaanpService,
        private spinner: NgxSpinnerService,
        private router: Router
    ) { }

    data: Perusahaanp = {};
    ngOnInit() {
        this.form();
    }




    dataForm: FormGroupTypeSafe<Perusahaanp>;
    form() {
        this.dataForm = this.fb.group<Perusahaanp>(
            {
                nama_perusahaan: new FormControl('', [Validators.required]),
                nama_pimpinan: new FormControl('', [Validators.required]),
                kecematan: new FormControl(''),
                kelurahan: new FormControl(''),
                klu: new FormControl(''),
                kode_pos: new FormControl(''),
                kota: new FormControl(''),
                alamat_email: new FormControl(''),
                no_telfon: new FormControl(''),
                npwp: new FormControl(''),
                alamat: new FormControl(''),
                fax: new FormControl(''),
                kpp: new FormControl(''),
                tanggal_pengukuhan: new FormControl(''),
                lokasi_pelaporan: new FormControl(''),
                jabatan: new FormControl(''),
                jenis_usaha: new FormControl('')

            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.payrollPerusahaanpService.postPayrollPerusahaanps(this.data).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/payroll/module/pengaturan/perusahaan/list'])
    }


}
