import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { CompanyPerusahaanService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { Perusahaan } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private companyPerusahaanService: CompanyPerusahaanService,
        private spinner: NgxSpinnerService,
        private router: Router
    ) { }

    data: Perusahaan = {};
    ngOnInit() {
        this.form();
    }

    dataForm: FormGroupTypeSafe<Perusahaan>;
    form() {
        this.dataForm = this.fb.group<Perusahaan>(
            {
                nama: new FormControl('', [Validators.required]),
                alamat: new FormControl('', [Validators.required]),
                email: new FormControl(''),
                website: new FormControl('', [Validators.required]),
                telfon: new FormControl(''),
                kota: new FormControl(''),
                propinsi: new FormControl(''),
                negara: new FormControl(''),

            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.companyPerusahaanService.postCompanyPerusahaans(this.data).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/company/module/perusahaan/list'])
    }

}
