import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { PayrollPerusahaanpService } from 'src/app/sdk/payroll/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { Perusahaanp } from 'src/app/sdk/payroll/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private payrollPerusahaanpService: PayrollPerusahaanpService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) { }

    data: Perusahaanp = {};
    id_perusahaan: string = ''
    ngOnInit() {
        this.id_perusahaan = this.activatedRoute.snapshot.paramMap.get('id')
        this.form();
        this.getData();
    }

    getData() {
        this.spinner.show()
        this.payrollPerusahaanpService.getPayrollPerusahaanpsId(this.id_perusahaan).subscribe(
            data=> {
                this.dataForm.setValue(data.data)
                this.spinner.hide()
            }
        )
    }


    dataForm: FormGroupTypeSafe<Perusahaanp>;
    form() {
        this.dataForm = this.fb.group<Perusahaanp>(
            {
                nama_perusahaan: new FormControl('', [Validators.required]),
                nama_pimpinan: new FormControl('', [Validators.required]),
                kecematan: new FormControl(''),
                kelurahan: new FormControl(''),
                klu: new FormControl(''),
                kode_pos: new FormControl(''),
                kota: new FormControl(''),
                alamat_email: new FormControl(''),
                no_telfon: new FormControl(''),
                npwp: new FormControl(''),
                alamat: new FormControl(''),
                fax: new FormControl(''),
                kpp: new FormControl(''),
                tanggal_pengukuhan: new FormControl(''),
                lokasi_pelaporan: new FormControl(''),
                jabatan: new FormControl(''),
                jenis_usaha: new FormControl(''),
                id_perusahaan: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),

            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.payrollPerusahaanpService.putPayrollPerusahaanpsId({id: this.id_perusahaan, body: this.data}).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/payroll/module/pengaturan/perusahaan/list'])
    }


}
