import { Component, OnInit } from '@angular/core';
import { PayrollPerusahaanpService } from 'src/app/sdk/payroll/services';
import { Perusahaanp } from 'src/app/sdk/payroll/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private payrollPerusahaanpService: PayrollPerusahaanpService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute,
        private router: Router
    ) { }

    id_perusahaan: string = ''
    toogleUpload = false

    dataPerusahaan: Perusahaanp = {}
    ngOnInit() {
        this.id_perusahaan = this.activatedRoute.snapshot.paramMap.get('id')
        this.getData()

    }


    getData() {
        this.payrollPerusahaanpService.getPayrollPerusahaanpsId(this.id_perusahaan).subscribe(
            data => {
                this.dataPerusahaan = data.data;


            }
        )
    }



    goToBack() {
        this.router.navigate(['/hr/payroll/module/pengaturan/perusahaan/list'])
    }

}
